@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  flex-direction: column;
  align-items: flex-start;
  width: 1029px;
  height: 1083px;
  margin: 0 auto;
  font-family: Avenir, sans-serif;
  display: flex;
}

input {
  background-image: url("Search.1bd77a3d.svg");
  background-position: 20px;
  background-repeat: no-repeat;
  background-size: 19px 19px;
  border: 1px solid #e1e7ed;
  width: 100%;
  padding: 25px 0 0 26px;
  font-weight: 400;
  line-height: 22px;
}

input::placeholder {
  color: #3d4e61;
  font-weight: 400;
}

form[data-qa="big"] {
  margin-top: 20px;
}

input[data-qa="keypress"] {
  background-position: 20px;
  background-repeat: no-repeat;
  background-size: 19px 19px;
  height: 70px;
  padding: 0 0 0 46px;
  font-size: 16px;
}

form[data-qa="small"] {
  width: 100%;
  margin-top: 20px;
}

form[data-qa="small"] input {
  color: #4e5152;
  background-image: url("Search.1bd77a3d.svg");
  background-position: 20px;
  background-repeat: no-repeat;
  background-size: 14px 14px;
  border: 1px solid #e1e7ed;
  border-radius: 6px;
  width: 100%;
  margin-bottom: 18px;
  padding: 0 0 0 46px;
  font-size: 14px;
  font-weight: 300;
  line-height: 16px;
}

form[data-qa="small"] input:hover {
  filter: drop-shadow(0 4px #00000040);
  border-color: #cbd6e2;
  font-weight: 300;
}

form[data-qa="small"] input:hover::placeholder {
  box-shadow: 2px 2px 4px #00000040;
}

form[data-qa="big"] input:hover {
  border-color: #cbd6e2;
  font-weight: 400;
  box-shadow: 0 3px 8px #3d4e6133, 0 4px 4px #00000040;
}
